import * as etch from 'etch';
import { handlePromise } from '../utils';
export async function renderActions(editor, act) {
    if (!act)
        return undefined;
    if (!act.length)
        return undefined;
    const maxActions = 4;
    return (etch.dom("ide-haskell-tooltip-actions", null,
        act.slice(0, maxActions).map((act) => (etch.dom("button", { className: "btn btn-xs", on: { click: () => handlePromise(act.apply()) } }, act.title))),
        act.length > maxActions ? (etch.dom("button", { className: "btn btn-xs", on: { click: click(editor) } }, "\u2026")) :
            null));
}
function click(editor) {
    return function () {
        atom.commands.dispatch(atom.views.getView(editor), 'ide-haskell:show-actions');
    };
}
