import { CompositeDisposable } from 'atom';
import { PriorityRegistry } from '../priority-registry';
import { selectAction } from './select-action';
export class ActionRegistry extends PriorityRegistry {
    constructor(pluginManager) {
        super();
        this.pluginManager = pluginManager;
        this.disposables = new CompositeDisposable();
        this.disposables.add(atom.commands.add('atom-text-editor.ide-haskell', {
            'ide-haskell:show-actions': async ({ currentTarget }) => {
                const editor = currentTarget.getModel();
                const act = await this.pluginManager.actionRegistry.getActions(editor, "context", editor.getSelectedBufferRange());
                if (act && act.length) {
                    const choice = await selectAction(act);
                    if (choice)
                        await choice.apply();
                }
            },
        }));
    }
    dispose() {
        super.dispose();
        this.disposables.dispose();
    }
    async getActions(editor, type, crange) {
        for (const { pluginName, handler, eventTypes } of this.providers) {
            if (!eventTypes.includes(type)) {
                continue;
            }
            const awaiter = this.pluginManager.getAwaiter(pluginName);
            const actions = await awaiter(() => handler(editor, crange, type));
            if (actions === undefined)
                continue;
            if (!actions.length)
                continue;
            return actions;
        }
        return undefined;
    }
}
