import { Button } from './output-panel-button';
import * as etch from 'etch';
export class OutputPanelButtons {
    constructor(props) {
        this.props = props;
        etch.initialize(this);
    }
    render() {
        return (etch.dom("ide-haskell-panel-buttons", null, this.renderButtons()));
    }
    async update(props) {
        this.props = props;
        return etch.update(this);
    }
    renderButtons() {
        return this.props.buttons.map((props) => (etch.dom(Button, { active: props.name === this.props.activeBtn, name: props.name, count: props.count, onClick: props.onClick })));
    }
}
