import * as etch from 'etch';
import { handlePromise } from '../utils';
export class TooltipMessage {
    constructor(source, message, actions) {
        this.source = source;
        this.tooltipClick = (e) => {
            if (!e.target)
                return;
            const htmlTarget = e.target;
            if (htmlTarget.matches('a')) {
                const href = htmlTarget.href;
                if (href) {
                    handlePromise(atom.workspace.open(`ide-haskell://hoogle/web/${href}`, {
                        searchAllPanes: true,
                        split: 'right',
                        activateItem: true,
                    }));
                }
            }
        };
        if (Array.isArray(message)) {
            this.message = message.map((m) => etch.dom("div", { key: m, innerHTML: m.toHtml() }));
        }
        else {
            this.message = [etch.dom("div", { key: message, innerHTML: message.toHtml() })];
        }
        if (actions) {
            handlePromise(actions.then(async (acts) => {
                this.actions = acts;
                return this.update();
            }));
        }
        etch.initialize(this);
    }
    render() {
        return (etch.dom("ide-haskell-tooltip-with-actions", null,
            this.renderTooltip(),
            this.actions ?? null));
    }
    async update() {
        return etch.update(this);
    }
    writeAfterUpdate() {
        if (this.element.parentElement) {
            this.element.parentElement.classList.add('ide-haskell');
        }
    }
    renderTooltip() {
        if (this.message.length) {
            return (etch.dom("ide-haskell-tooltip", { on: { click: this.tooltipClick }, dataset: { source: this.source } }, this.message));
        }
        else {
            return null;
        }
    }
}
